import React, { Component } from 'react'

import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import TodoList from '../todo-list';
import ItemStatusFilter from '../item-status-filter';
import ItemAddForm from '../item-add-form'
import AppFooter from '../app-footer'

import './app.css'


export default class App extends Component {
    maxId = 100;

    state = {
        todoData: [
            this.createTodoItem('Drink cofee Drink cofee'),
            this.createTodoItem('Make Awesome app'),
            this.createTodoItem('Go to sleep')
        ],
        term: '',
        filter: 'active' //  all, active, done
    }

    createTodoItem(label) {
        return {
            id: this.maxId++,
            label,
            done: false,
            important: false
        }
    }

    deleteItem = (id) =>{
        this.setState(({todoData}) => {
            const idx = todoData.findIndex((el) => el.id === id)
            const before = todoData.slice(0, idx)
            const after = todoData.slice(idx + 1)
            const newArr = [ ...before, ...after]
            return {
                todoData: newArr
            }  
        })
    }

    addItem = (text) => {
        const newItem = this.createTodoItem(text)
        this.setState( ({ todoData }) => {
            const newArr = [newItem, ...todoData, ]
            return {
                todoData: newArr
            }
        })
    }

    searchItem = (items, term) => {
        if (term.length === 0){
            return items
        }
        return items.filter((item) => {
            return item.label
                        .toLowerCase()
                        .indexOf(term.toLowerCase()) > -1
        })
    }

    filterItem = (items, filter) => {
        switch(filter) {
            case 'all':
                return items
                
            case 'active':
                return items.filter((item) => !item.done);
                
            case 'done':
                return items.filter((item) => item.done)
            
            default:
                return items
        }
    }
    
    onSearchChange = (term) => {
        this.setState({ term })
    }

    onFilterChange = (filter) => {
        console.log('filter', filter)
        this.setState({ filter })
    }

    onToggleProperty = (arr, id, propName) => {
            //1. update object
            const idx = arr.findIndex((el) => el.id === id)
            const oldItem = arr[idx]
            const newItem = {
                ...oldItem,
                [propName]: !oldItem[propName]
            }
            //2. return & construct new Arr
            return [ 
                    ...arr.slice(0, idx),
                    newItem,
                    ...arr.slice(idx + 1)
            ]
    }

    onToggleDone = (id) => {
        this.setState(({ todoData }) => {
            return {
                todoData: this.onToggleProperty(todoData, id, 'done')
            }
        })
            
    }

    onToggleImportant = (id) => {
        this.setState(({ todoData }) => {
            return {
                todoData: this.onToggleProperty(todoData, id, 'important')
            }
        })
    }

    render(){
        const { todoData, term, filter} = this.state
        const visibleItems = this.filterItem(
            this.searchItem(todoData, term),
            filter)
        const doneCount = todoData
            .filter((item) => item.done === true)
            .length
        const todoCount = todoData.length - doneCount
        
        return(
            <div className="todo-app">
                <AppHeader toDo={ todoCount } done={ doneCount }/>
                <div className="top-panel d-flex">
                    <SearchPanel onSearchChange={ this.onSearchChange }/>
                    <ItemStatusFilter 
                        onFilterChange={ this.onFilterChange }
                        filter={ filter } />
                </div>

                <div className="d-flex">
                    <ItemAddForm 
                        onAdded={ this.addItem }
                    />
                </div>

                <TodoList 
                    todos={ visibleItems }
                    onDeleted={ this.deleteItem }
                    onToggleDone={ this.onToggleDone }
                    onToggleImportant={ this.onToggleImportant }
                />
                <AppFooter />
            </div>
        )
    }
}